@use "utilities" as *;

.nav {
  display: none;
  position: absolute;
  bottom: rem(20);
  left: 50%;
  transform: translate(-50%);
  width: 100%;
  z-index: 1;

  &__list {
    display: flex;
    gap: calc(2.5rem + (100vw - 320px) / 6);
    justify-content: center;
    align-items: center;

    @media screen and (max-width: 320px) {
      gap: rem(30);
    }
  }

  &__title {
    display: none;
  }

  @include breakpoint(medium) {
    display: block;
    position: absolute;
    bottom: rem(38);
    left: 50%;
    transform: translate(-50%);

    &__list {
      display: flex;
      gap: 15vw;
    }

    &__title {
      display: none;
    }
  }

  @include breakpoint(large) {
    position: static;
    transform: none;
    width: auto;

    &__list {
      gap: rem(10);
    }

    &__title {
      display: inline-block;
      margin-left: rem(5);
    }
  }
}
